(function() {
    'use strict';


    /* Controllers */

    angular.module('websiteApp').controller('loginController', loginController);

    loginController.$inject = ['$window', '$interval', 'Logger', 'User'];

    function loginController($window, $interval, Logger, User) {

        /* jshint validthis: true */
        var vm = this;

        /**********  Data Binding For CSS style   **********/
        vm.css = {
            loginFormErrorMessage : 0,
            signupType : 'mobile',
            signupFirstTime : true,
            signupSMSCountDown : 60,
            signupSMSSendButton : true
        };


        /**********  Data Binding For ViewModel  **********/
        vm.data = {
            newUser : {
                username : '',
                password : ''
            },

            signupUser : {
                email : '',
                mobile : '',
                smscode : '',
                username : '',
                password : ''
            }
        };


        /**********  Event Center  **********/
        vm.event = {
            login : login,
            logout : logout,
            changeSignupType : changeSignupType,
            getSignupSMS : getSignupSMS,
            signupSMS : signupSMS
        };


        function login(form){

            if (form.$invalid) {
                form.username.$setDirty();
                form.password.$setDirty();
                return ;
            }

            User.login(vm.data.newUser).then(function(data){
                console.log(data);
                $window.location.href = '/';
            }).catch(function(err){
                if (err && err.data && err.data.code){
                    vm.css.loginFormErrorMessage = err.data.code;
                }
                Logger.errorXHR(err);
            });
        }


        function logout(){

            User.logout().then(function(data){
                console.log(data);
                $window.location.href = '/';
            }).catch(function(err){
                Logger.errorXHR(err);
            });

        }


        function changeSignupType(type){
            vm.css.signupType = type ;
        }


        function getSignupSMS(form){

            if (form.mobile.$invalid) {
                form.mobile.$setDirty();
                return ;
            }

            vm.css.signupFirstTime = false;
            vm.css.signupSMSSendButton = false;

            var timer = $interval(function () {
                vm.css.signupSMSCountDown = vm.css.signupSMSCountDown - 1;
                if (vm.css.signupSMSCountDown <=0) {
                    $interval.cancel(timer);
                    vm.css.signupSMSCountDown = 60;
                    vm.css.signupSMSSendButton = true;
                }
            }, 1000);

            User.getSMS(vm.data.signupUser.mobile, 'signup').then(function(data){
                console.log(data.data);
            }).catch(function(err){
                Logger.errorXHR(err);
            });
        }


        function signupSMS(form){

            if (form.$invalid) {
                form.smscode.$setDirty();
                form.username.$setDirty();
                form.password.$setDirty();
                return ;
            }

            User.signup(vm.data.signupUser).then(function(data){
                console.log(data);
            }).catch(function(err){
                if (err && err.data && err.data.code){
                    vm.css.loginFormErrorMessage = err.data.code;
                }
                Logger.errorXHR(err);
            });
        }



        /**********  App Init  **********/

        function appInit (){

        }

        function appReRun(){

        }

        appInit();

    }



})();


(function() {
  'use strict';


  /* Services Model */


// Demonstrate how to register services
// In this case it is a simple value service.

  angular.module('websiteApp').factory('Logger', loggerService);

  loggerService.$inject = ['$log'];

  function loggerService($log) {
      return {
          debug: debug,
          errorXHR: errorXHR
      };

      function errorXHR(err) {
          $log.error('XHR Failed. ' );
          $log.debug(err.data );
      }

      function debug(info) {
          $log.debug('Debug Info : ' + info );
          return info;
      }

  }
})();


(function() {
  'use strict';


  /* Services Model */


// Demonstrate how to register services
// In this case it is a simple value service.

  angular.module('websiteApp').factory('User', userService);

  userService.$inject = ['$http', '$localStorage'];

  function userService($http, $localStorage) {
      return {
          login: login,
          logout: logout,
          getSMS: getSMS,
          signup: signup,
      };

      function login(user) {
          return $http.post('/api/user/login', user).then(getUserLogin);
            //   .catch(Logger.errorXHR);

          function getUserLogin(response) {
              if (response.data && response.data.accessToken) {
                    $localStorage.access_token = response.data.accessToken;
                }

              return response.data;
          }
      }

      function logout() {
          var token = $localStorage.access_token || '';
          return $http.post('/api/user/logout', {accessToken:token});
      }

      function getSMS(mobile, messageType) {
          messageType = messageType || 'signup';
          return $http.post('/api/user/verify/sms', {mobile : mobile, messageType:messageType});
      }

      function signup(user) {
          return $http.post('/api/user/signup', {
              email    : user.email || '',
              mobile   : user.mobile || '',
              smscode  : user.smscode || '',
              username : user.username,
              password : user.password
          });
      }
  }
})();



